.card {
  border: 1px solid #f8f8f8;
  padding: 30px;
  width: 220px;
  border-radius: 40px;
  transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;

  &:hover {
    box-shadow: 0 20px 35px rgba(0, 0, 0, 0.06);
    transform: translateY(-5px);
  }

  h5 {
    font-weight: 400;
    font-size: 14px;
    margin: 15px 0;
  }

  span {
    opacity: 0.5;
    font-size: 13px;
    text-transform: uppercase;
  }

  b {
    font-size: 14px;
  }
  .favorite {
    position: absolute;
    cursor: pointer;
  }

  &Bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &Price {
      display: flex;
      flex-direction: column;
    }
    .plus {
      cursor: pointer;
    }
  }
}
